.App {
  text-align: left;
}

.container {
  min-height: 100vh;
  width: 80%;
  max-width: 850px;
  margin: auto;
  
}

nav {
  padding: 1em 0;
  background: linear-gradient(to bottom, #333, #fff);
  
}

nav ul {
  display: flex;
  justify-content: space-around;
  align-items: center;
}

nav li {
  list-style: none;
  text-transform: uppercase;
  letter-spacing: 5px;  
}

.link {
  text-decoration: none;
  color: #fff;
  
}

p {
  text-align: justify;
  line-height: 1.5em;
}

/* Article list Page */
.link-list {
  text-decoration: none;
  color: #000
}

.article-title {
  color: #333;
  transition: .2s;
  letter-spacing: 3px;
}

.article-title:hover {
  color: #000;
  text-shadow: 1px 3px 5px #000;
}

.article-paragraph {
 color: #333
}